<template>
    <div class="suggesList-box">
        <!-- <Loading v-if="!suggesList.order"></Loading> -->
        <div class="suggesList-title">
            <span>搜索提示：</span>
            <span class="suggesList-title-first">{{this.keyword}}</span>
        </div>
        <!-- <p v-show="suggesList.length<=0">暂无结果</p> -->
        <div class="suggesList-song">
            <ul>
                <!-- 歌曲 -->
                <template v-if="suggesList">
                    <li v-for="i in suggesList" :key="i.id" class="suggesList-song-ul-li" @click="$emit('change',i.name)">
                        {{i.name}}
                    </li>
                </template>
                <!-- 歌手 -->
                <template v-if="suggesList.artists">
                    <li v-for="i in suggesList.artists" :key="i.id" class="suggesList-song-ul-li" @click="$emit('change',i.name)">
                        {{i.name}}
                    </li>
                </template>
                <!-- 专辑 -->
                <template v-if="suggesList.albums">
                    <li v-for="i in suggesList.albums" :key="i.id" class="suggesList-song-ul-li" @click="$emit('change',i.name)">
                        {{i.name}}
                    </li>
                </template>
            </ul>
        </div>
    </div>
</template>
<script>
// import Loading from '../components/Loading'
export default {
    components:{
        //Loading,
    },
    props:["keyword"],
    data(){
        return{
            // 搜索建议清单
            suggesList:[],
            // typeIndex:1,
        }
    },
    watch:{
        keyword(val){
             
            this.suggesListRender(val);
        }
    },
    created(){
        this.suggesListRender(this.keyword);
    },
    methods:{
        suggesListRender(val){
            if(val == "") return false;
            
            this.axios.get("/search/suggest?keywords="+val).then(res=>{
                //this.suggesList=[]; //清空原数组
                this.suggesList = res.data.result.songs;
                 console.log(res.data.result.songs==undefined);
                 if(res.data.result.songs==undefined){
                     this.suggesList=[]; 
                 }
            })
                 
                 
                 
        }
    }
}
</script>
<style lang="less" scoped>
.suggesList-title{
    width: 100vw;
    height: 13.333vw;
    font-size: 3.267vw;
    font-weight: 400;
    line-height: 13.333vw;
    padding: 0 5.333vw;
    box-sizing: border-box;
    color: #000;
    text-align: left;
    .suggesList-title-first{
        color: #598ecd;
    }
}
.suggesList-song-ul-li{
    font-size: 4.267vw;
    color: #000;
    width: 100vw;
    height: 13.333vw;
    line-height: 13.333vw;
    padding-left: 12vw;
    padding-right: 5.333vw;
    box-sizing: border-box;
    background-image: url(data:image/png;base64,);
    background-size: 4.533vw 4.533vw;
    background-position: 5.333vw;
    background-repeat: no-repeat;
    text-align: left;
}
</style>