<template>
  <div class="searchresult">
    <p>
      搜索结果:{{ keyword }}
    </p> 
    <ul>
       <!-- <li>
         <img :src="`${searchresult[0].artists[0].img1v1Url}`" alt="">
       </li> -->
      <!-- <div class="albument" v-if="albument">
        <img :src="`${albument.picUrl}`" alt="">
        <div>
          <p>《{{albument.name}}》</p>
          <p>{{albument.artist.name}}</p>
          <p>{{albument.publishTime | formatDate("m月d日")}}</p>
        </div>
      </div> -->
      <newest-music v-for="item in searchresult" :item="item" 
      :key="item.id"
      :keyword="keyword"
      @play="$emit('play', $event)"
      :musicID="musicID"></newest-music>
    </ul>
  </div>
</template>
<script>
import NewestMusic from "@/components/NewestMusic";
import defaultPic from "@/assets/music.png";
export default {
  props: {
    keyword: String,
     musicID:{
      type:[Object,null]
    }
  },
  data(){
        return{
            searchresult:[],
            albument:null
        }
  },
  components: {
    NewestMusic,
  },
  created() {
    this.axios.get("/search?keywords=" + this.keyword).then((res) => {
      console.log(res);
      this.searchresult=res.data.result.songs.map((data)=>{
          data.song={
              artists:data.artists,
              album:data.album,
              alias: [],
          }
          data.picUrl = defaultPic
          return data
      })
    console.log(this.searchresult);
    });
    this.axios.get("/search?keywords=" + this.keyword +"&type=10").then((res) => {
     this.albument = res.data.result.albums[0]
     console.log(res)
    });
  },
  filters: {
    formatDate(value, fstr) {
      let d = new Date(value);
      let formatD = {
        "m+": d.getMonth(),
        "d+": d.getDate(),
      };
      for (let i in formatD) {
        let reg = new RegExp(i);
        let val = formatD[i];
        val = val < 10 ? "0" + val : val;
        fstr = fstr.replace(reg, val);
      }
      return fstr;
    },
  },
};
</script>
<style lang="less" scoped>
    .searchresult{
      margin: 10px;
      p{
        font-size: 5vw;
        margin-left: 2vw;
        &:first-child{
          font-size: 4.5vw;
        }
      }
      ul{
        .albument{
          margin-top: 4vw;
          display: flex;
          justify-content: space-around;
          align-items: center;
          img{
            width: 70px;
            height: 70px;
            border-radius: 50%;
          }
          div{
            width: 60%;
            p{
              margin: 1vw 0 1vw 0;
              font-size: 4vw;
              &:first-child{
                margin-left: -1px;
              }
              &:last-child{
                font-size: 3vw;
                color:#666;
              }
            }
          }
        }
      }
    }
</style>