<template>
    <div class="searchs1">
        <div class="search-box" ref="searchBox">
            <div class="search-input-box">
            <router-link class="iconfont icon-back_icon" tag="div" to="/" v-show="!(keyword)"></router-link>
                <div class="search-input-back">
                    <div class="iconfont icon-sousuo"></div>
                    <input type="text" placeholder="搜索歌曲、歌手、专辑" class="search-input" v-model="keyword" @input="inputing=true">
                    <div class="iconfont icon-x search-iocr" v-show="keyword" @click="pruge"></div>
                </div>
            </div>
            <div class="search-input-butt" v-show="keyword" @click="change(keyword)">搜索</div>
        </div>
        <component :musicID="musicID" @play="$emit('play',$event)" :valarray="valarray" :is="searchType" :keyword="keyword" @change="change" :node="node"></component>
        <Loading v-show="flag"></Loading>
        <!-- <SearchHotcomponent v-show="!(this.keyword)"></SearchHotcomponent>
        <SearchSugght v-show="this.keyword&&this.inputing" :keyword="this.keyword" @change="change"></SearchSugght>
        <SearchResult></SearchResult> -->
    </div>
</template>
<script>
// 搜索组件
import SearchHotcomponent from '../components/SearchHotcomponent'
// 搜索建议组件
import SearchSugght from '../components/SearchSugght'
// 搜索结果
import SearchResult from '../components/SearchResult'
import Loading from '../components/Loading'
export default {
    props:['musicID'],
    components:{
        SearchHotcomponent,
        SearchSugght,
        SearchResult,
        Loading,
    },
    data(){
        return{
            keyword:null,
            inputing:false,
            searchType:"SearchHotcomponent",
            flag:true,
            node:null,
            valarray:[]
        }
    },
    watch:{
        keyword(val){
            if(val == ""){
                this.flag=false;
                this.searchType = "SearchHotcomponent";
            }else if(this.inputing){
                this.flag=false;
                this.searchType = "SearchSugght";
            }
        }
    },
    mounted(){
        this.flag =false;
        // let that=this;
        console.log(this.musicID)
         this.node = this.$refs.searchBox;
        let top = this.node.offsetTop+this.node.offsetHeight-20;
        let that=this;
        window.onscroll = function(){
            //滚动条滚动时，距离顶部的距离
            let scrollTop = document.documentElement.scrollTop||document.body.scrollTop;
            if(scrollTop>top){
                that.node.classList.add("active");
            }else{
                that.node.classList.remove("active");
            }
        }
    },
    methods:{
        change(val){
            this.flag=false;
            this.inputing =false;
            this.keyword = val;
            this.searchType = "SearchResult";
            this.valarray.push(val);
            let newArr = [];
            for (var i = 0; i < this.valarray.length; i++) {
            if (newArr.indexOf(this.valarray[i]) === -1) {
            newArr.push(this.valarray[i]);
          }
        }
        newArr = newArr.reverse();
         this.valarray = newArr;
        },
        pruge(){
            console.log(this.keyword)
            this.keyword = "";
        }
    }
}
</script>
<style lang="less" scoped>
.searchs1{
     width:100%;
    .search-box{
        width: 100%;
        box-sizing: border-box;
        display: flex;
        padding: 20px 0;
        padding-right: 2.3vw;
        background-color: #fff;
        &.active{
            position: fixed;
            top: 0;
            left: 0;
        }
        .search-input-box{
            height: 10.667vw;
            width: 100%;
            position: relative;
            display: flex;
            justify-content: center;
            .icon-back_icon{
                display:flex;
                align-items: center;
                margin-right:3vw;
                font-size:5vw;
            }
            .search-input-back{
                width: 79.467vw;
                background: #f7f7f7;
                border-radius: 5.333vw;
                padding: 2.4vw;
                box-sizing: border-box;
                display: flex;
                .icon-sousuo{
                    font-size:5.5vw;
                }
                .search-ico{
                    margin: .533vw 2.133vw 0 1.067vw;
                    width: 4.533vw;
                    height: 4.533vw;
                    background-image: url(data:image/png;base64,);
                    background-repeat: no-repeat;
                    background-size: 4.533vw;
                    background-position: 50%;
                }
                .search-input{
                    background: none;
                    outline: none;
                    border: 0;
                    width: 58.667vw;
                    height: 5.867vw;
                    padding-right: 2.4vw;
                    box-sizing: border-box;
                    font-size: 4.267vw;
                    font-weight: 400;
                    color: #000;
                    line-height: 6vw;
                }
                .icon-x{
                    border-radius: 50%;
                    width: 5.867vw;
                    height: 5.867vw;
                    margin-left:5vw;
                    display:flex;
                    align-items: center;
                    justify-content: center;
                    // background-color: #e6e6e6;
                    // background-image: url(data:image/png;base64,);
                    // background-repeat: no-repeat;
                    // background-size: 5.867vw;
                    // background-position: 50%;
                }
            }
        }
        .search-input-butt{
            width: 40px;
            height: 5.867vw;
            margin-top: 2.133vw;
            font-size: 4.267vw;
            font-family: PingFangSC-Regular,PingFang SC;
            font-weight: 400;
            color: #262338;
            color: rgba(38,35,56,.5);
            line-height: 6vw;
            margin-left: -4vw;
        }
    }
}
</style>