<template>
    <div class="searchhotcomponent">
       <p>热门搜索:</p> 
        <ul>
            <li v-for="(searchhot,index) in hot_detail" @click.stop="$emit('change',searchhot.searchWord)" :key="index">{{searchhot.searchWord}}</li>
        </ul>
        <p>搜索历史:</p>
        <ul>
            <li v-for="(valarrays,index) in valarray" :key="index" @click.stop="$emit('change',valarrays)">{{valarrays}}</li>
        </ul>
    </div>
</template>
<script>
export default {
    props:['keyword','valarray'],
    data(){
        return{
          hot_detail:[]
        }
    },
    created() {
        this.$axios.get("/search/hot/detail").then((data) => {
            this.hot_detail=data.data.data.slice(0, 10);
          console.log(this.keyword);
    })
    },
}
</script>
